.menu {
  box-sizing: border-box;
  height: 100vh;
  width: 100%;
  max-width: 100vw;
  background-color: #c2c2c2;
  font-weight: 600;
  text-transform: uppercase;
  overflow: auto;

  &__content {
    box-sizing: border-box;
    width: 100%;

    @include content-padding-inline;
  }

  &__top {
    margin-bottom: 24px;
    position: sticky;
    top: 0;
    z-index: 1;
  }

  &__nav {
    margin-bottom: 40px;
  }

  &__phone-number {
    display: block;
    margin-bottom: 16px;
    color: #1d1d1d;
    text-decoration: none;
    font-size: 16px;
    line-height: 27px;
  }

  &__book-a-test-ride {
    display: block;
    width: fit-content;
    padding-bottom: 7px;
    border-bottom: 1px solid #1d1d1d;
    color: #1d1d1d;
    text-decoration: none;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 2px;
  }

  @include on-tablet {
    &__top {
      margin-bottom: 8px;
    }
  }
}
